/* Main layout styles */
.header {
    width: 100%;
    height: 40px;
    background: rgb(200, 37, 56);
    line-height: 40px;
    color: white;
}
.footer {
    width: 100%;
    height: 20px;
    line-height: 20px;
    background: rgb(255, 247, 14);
    color: white;
    font-size: 7pt;
    text-align: center;
}
.content {
    width: 100%;
    height: calc(100% - 60px);
}
.logouticon {
    height: 16px;
    float: right;
    margin-top: 12px;
    cursor: pointer;
}

.menubar {
    float: left;
    width: 196px;
    height: calc(100% - 4px);
    padding: 2px;
    background: whitesmoke;
}

.outlet {
    float: left;
    width: calc(100% - 200px);
    height: 100%;
}

/* Menu styles */
.menuheader {
    width: 100%;
    height: 30px;
    background: rgb(12, 213, 248);
    line-height: 30px;
}

.menuheader img {
    height: 16px;
    float: left;
    padding-left: 5px;
    margin-top: 6px;
    cursor: pointer;
}

.menuheader label {
    color: white;
    font-weight: bold;
    padding-left: 10px;
    cursor: pointer;
    float: left;
}

.menu {
    width: 100%;
    height: calc(100% - 30px);
    overflow: auto;
}

.mlist {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
}

.mlist li label {
    display: block;
    padding: 8px 5px;
    cursor: pointer;
    background: #BAC6DA;
    border-top: 2px solid white;
}

.mlist li label:hover {
    background: #B6DFE3;
}

.mlist li div {
    width: 100%;
    display: none;
}

.mlist li div label {
    background: #E9EEF3;
    cursor: pointer;
    display: block;
    padding: 8px 5px;
    border-top: 2px solid white;
}

/* Outlet styles */
.outlet #titlebar {
    background: whitesmoke;
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-size: 10pt;
    font-weight: bold;
}

.outlet #module {
    width: 100%;
    height: calc(100% - 30px);
    border: none;
}
